import "core-js/modules/es.array.push.js";
import { mapMutations, mapState } from "vuex";
import { Search } from "@element-plus/icons";
import { ElNotification } from "element-plus";
export default {
  name: "PredictComponent",
  data() {
    return {
      dialogVisible: false,
      form: {
        fileName: '',
        modelType: '酶',
        //文字转数字
        //大小值转化数组
        model_name: ""
      }
    };
  },
  computed: {
    Search() {
      return Search;
    },
    ...mapState(["modelInfoList", "filename"])
  },
  beforeRouteEnter(to, from, next) {
    console.log(to);
    next(vm => {
      if (to.query.model_type !== undefined) {
        // 修改模型类型
        switch (to.query.model_type) {
          case '0':
            vm.$data.form.modelType = "酶";
            break;
          case '1':
            vm.$data.form.modelType = "离子通道";
            break;
          case '2':
            vm.$data.form.modelType = "G蛋白偶联受体";
            break;
          case '3':
            vm.$data.form.modelType = "核受体";
            break;
        }
        // 修改模型名称
        vm.$data.form.model_name = to.query.model_name + '-' + to.query.id;
      }
    });
  },
  methods: {
    ...mapMutations(["getFileName"]),
    predictSubmit() {
      this.dialogVisible = false;
      if (this.form.fileName.trim() !== "" && this.form.model_name.trim() !== "" && this.filename.trim() !== "") {
        let model_type = 0;
        switch (this.form.modelType) {
          case "酶":
            model_type = 0;
            break;
          case "离子通道":
            model_type = 1;
            break;
          case "G蛋白偶联受体":
            model_type = 2;
            break;
          case "核受体":
            model_type = 3;
            break;
        }
        let params = {
          predict_data_path: this.filename.trim(),
          model_type: model_type,
          model_name: this.form.model_name.trim(),
          file_name: this.form.fileName.trim()
        };
        window.pyqt5.predict.main_predict(JSON.stringify(params));
        this.$router.push({
          name: 'homepage'
        });
        this.setCurrentPage(0);
      } else {
        ElNotification({
          title: '参数不足',
          message: '请再次查看请求的参数是否配置完全',
          duration: 0,
          type: 'error'
        });
      }
    },
    confirmSubmit() {
      this.dialogVisible = true;
    }
  }
};